<template>
  <div class="shop_list_all">
        <ul class="shop_list_ul">
          <router-link tag="li" :to="{path: 'shop', query:{geohash, id: item.id}}" v-for="item in shoplist" :key="item.id" class="shop_li">
            <div class="shop_list_imgs">
              <img :src="'/api/img/'+ item.image_path" class="shop_img">
            </div>
            <div class="shop_right">
              <div class="shop_detail">
                <h4 :class="item.is_premium? 'premium': ''" class="shop_h4">{{item.name}}

                </h4>
                <ul class="shop_detail_ul">
                  <li v-for="item in item.supports" :key="item.id" class="supports">
                    <span class="supports_span">{{item.icon_name}}</span>
                  </li>
                </ul>
              </div>
              <h5 class="shop_h5">
                <div class="rating_order_num_left">
                  <div class="rating_section">
                    <el-rate v-model="item.rating" disabled show-score text-color="#ff9900" score-template="{value}">
                    </el-rate>
                  </div>
                  <div class="order_section">
                    月售{{item.recent_order_num}}单
                  </div>
                </div>
                <div class="rating_order_num_right">
                  <span class="h5_span1" v-if="item.delivery_mode">{{item.delivery_mode.text}}</span>
                  <span class="h5_span2">准时达</span>
                </div>
              </h5>
              <h5 class="shop_bottom">
                <p class="shop_bottom_p1">
                  ¥{{item.float_minimum_order_amount}}起送
                  <span class="shop_bottom_span1">/</span>
                  {{item.piecewise_agent_fee.tips}}
                </p>
                <p class="shop_bottom_p2">
                  <span class="shop_bottom_span3">{{item.distance}}/</span>
                  <span class="shop_bottom_span4">{{item.order_lead_time}}</span>
                </p>
              </h5>

            </div>
          </router-link>
        </ul>
      </div>
</template>

<script type="text/ecmascript-6">
import {api6,api8 } from "../../service/getData";
import { Loading } from "element-ui";
export default {
  name:"shoplist",
  data() {
    return {
      geohash:"",
          shoplist: [],

    }
  },
  created(){
   
       let loadingInstance1 = Loading.service({ fullscreen: true });
       this.geohash = this.$route.query.geohash;
       /**获取商铺列表 */
       this.axios.get(api8).then(shoplist => {
      //关闭加载提示；
      loadingInstance1.close();
      // console.log(shoplist);
      this.shoplist = shoplist.data;
      // console.log(shoplist.name);
      // console.log(333);
    });
  },
  components: {

  }
}
</script>

<style scoped>
/*商铺列表*/
.shop_li {
  width: 100%;
  height: 1.1rem;
  display: flex;
  border-bottom: 1px solid rgb(228, 228, 228);
  
}
.shop_list_imgs {
  width: 0.78rem;
  height: 0.78rem;
  margin-top: 0.15rem;
  padding-left: 0.08rem;
}
.shop_img {
  width: 0.78rem;
  height: 0.78rem;
}
.shop_detail {
  display: flex;
  justify-content: flex-start;
  align-content: center;
}
.shop_h4 {
  width: 2.5rem;
  color: rgb(51, 51, 51);
  font-size: 0.16rem;
  font-weight: 700;
  padding-left: 0.2rem;
  margin-top: 0.15rem;
  overflow: hidden;
}
.premium::before {
  content: "品牌";
  display: inline-block;
  font-size: 0.1rem;
  line-height: 0.2rem;
  color: #333;
  background-color: #ffd930;
  padding: 0 0.1rem;
  border-radius: 0.1rem;
  margin-right: 0.1rem;
}
.shop_detail_ul {
  display: flex;
  justify-content: flex-end;
  width: 1rem;
 
}
.supports {
  display: flex;
  font-size: 0.12rem;
  color: rgb(153, 153, 153);
  margin-top: 0.12rem;
  margin-left: 0.05rem;
  
}
.supports_span{
  margin-right:0.1rem;
}

.shop_h5 {
  display: flex;
  justify-content: space-between;
  padding-top: 0.1rem;
  padding-left:0.1rem;
  height: 0.18rem;
}
.rating_order_num_left {
  display: flex;
  justify-content: flex-start;
}
.el-rate {
  display: inline-block;
  padding-left: 0.1rem;
}
.order_section {
  display: inline-block;
  font-size: 0.13rem;
  color: rgb(154, 154, 154);
  margin-left: 0.05rem;
  margin-top: 0.05rem;
}
.rating_order_num_right {
  transform: scale(0.8);
}
.h5_span1 {
  margin-left: 0.4rem;
  font-size: 0.05rem;
  color: white;
  background-color: rgb(49, 144, 232);
  padding: 0.04rem 0.08rem;
  border-radius: 0.08rem;
  margin-left:-1.3rem;
}
.h5_span2 {
  font-size: 0.05rem;
  color: rgb(154, 154, 154);
  border: 1px solid #3190e8;
  padding: 0.04rem 0.08rem;
  border-radius: 0.08rem;
  margin-left: 0.01rem;
}
.shop_bottom{
   display: flex;
   justify-content: space-between;
   font-size:0.15rem;
   padding-top:0.15rem;
   transform: scale(0.9);

}
.shop_bottom_p1{
  color:rgb(102, 102, 102);
  padding-left:0.05rem;
}
.shop_bottom_span3{
   color:rgb(102, 102, 102);
   
}
.shop_bottom_span4{
  color:rgb(49, 144, 235);
}
</style>
<style>
.el-rate__item {
  width: 0.15rem;
}
.el-rate__icon {
  font-size: 0.12rem;
}
.el-rate__text {
  font-size: 0.12rem;
}
.el-carousel__button {
  background-color: rgb(190, 190, 190);
  width: 10px;
  height: 10px;
  border: 1px solid rgb(204, 204, 204);
  border-radius: 50%;
  margin-right: 0.1rem;
}
</style>




