<template>
  <div class="login">
    <div class="header_top">
      <router-link to="/home">
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <span class="header_name">密码登录</span>
    </div>
    <div class="login_all">
      <div class="login_input1">
        <input type="text" placeholder="账号" style="outline:none" v-model.lazy="userAccount" class="login_input login_input_top">
      </div>
      <div class="login_input2">
        <input type="password" v-if="!showpassword" placeholder="密码" style="outline:none" v-model="passWord" class="login_input login_input_center">
        <input type="text" v-else placeholder="密码" style="outline:none" v-model="passWord" class="login_input login_input_center">
        <div class="login_switch" :class="{change_to_text: showpassword}">
          <el-switch v-model="value2" active-color="#13ce66" inactive-color="#ff4949" class="login_switch1" :class="{trans_to_right: showpassword}" @click="changepassword">
            <span>abc</span>
            <span>...</span>
          </el-switch>
        </div>

      </div>
      <div class="login_input3">
        <input type="text" placeholder="验证码" style="outline:none" v-model="codeNumer" class="login_input login_input_bottom">
        <img @click="getCode()" :src="code&&code" alt="" class="login_img">
        <div class="change_img">
          <p class="img_p1" @click="getCode()">看不清</p>
          <p class="img_p2" @click="getCode()">换一张</p>
        </div>
      </div>
    </div>
    <div class="login_bottom">
      <p class="login_p1">
        温馨提示：未注册过的账号，登录时将自动注册
      </p>
      <p class="login_p2">
        注册过的用户可凭账号密码登录
      </p>

    </div>
    <el-button type="success login_button" @click="login">登录</el-button>
    <router-link to="/forget" class="login_forget">重置密码？</router-link>
  </div>
</template>

<script type="text/ecmascript-6">
// import {mapState, mapMutations} from 'vuex'
import { api10, api11 } from "../../service/getData";

export default {
  name: "login",
  data() {
    return {
      code: "",
      passWord: "",
      showpassword: false,
      codeNumer: "",
      phoneNumber: null, //电话号码
      userInfo: null, //获取到的用户信息
      userAccount: null, //用户名
      username: "",
      value1: true,
      value2: true
    };
  },
  created() {
    this.getCode();
  },
  methods: {
    // ...mapMutations([
    //     'RECORD_USERINFO',
    // ]),
    //是否显示密码
    changepassword() {
      this.showpassword = !this.showpassword;
    },
    getCode() {
      this.axios({
        method: "post",
        url: api10,
        withCredentials: true // 默认false
      }).then(res => {
        // console.log("tap", res);
        if (res.data.status == 200) {
        }
        this.code = res.data.code;
      });
    },
    login() {
      /**获取用户信息 */
      this.axios({
        method: "post",
        url: api11,
        withCredentials: true, // 默认的
        data: {
          captcha_code: this.codeNumer,
          password: this.passWord,
          username: this.userAccount
        }
      }).then(res => {
        if (res.data.status == 0) {
          alert(res.data.message);

          this.$router.go(0);
        } else {
          this.$store.commit("setusername", this.userAccount);
          this.$router.push({ name: "mine" });
          var obj = res.data;
          var str = JSON.stringify(obj);

          localStorage.obj = str;
        }

        console.log("---", res);
        //给vuex保存用户信息
      });
    }
  },
  computed: {
    //判断
    rightPhoneNumber: function() {
      return /^[A-Za-z0-9]{4,16}$/;
    },
    setusername() {
      return this.$store.state.username;
    }
  },
  components: {}
};
</script>

<style scoped>
/*顶部 */
.login {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
.header_name {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left: 0;
  transform: translateX(-50%);
}
/*账号密码*/
.login_all {
  position: absolute;
  width: 100%;
  top: 0.5rem;
  left: 0;
  background-color: white;
  margin-top: 0.2rem;
}

.login_input1 {
  width: 100%;
  height: 0.5rem;
  border: none;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  font-size: 0.2rem;
  padding: 0.1rem 0;
}
.login_input_top {
  padding: 0 0.2rem;
}
.login_input2 {
  position: relative;
  width: 100%;
  height: 0.5rem;
  padding: 0.1rem 0;
  border: none;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  font-size: 0.2rem;
}
.login_input_center {
  padding: 0 0.2rem;
}
.login_switch {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
}
.login_input3 {
  width: 100%;
  height: 0.5rem;
  padding: 0.1rem 0;
  border: none;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  font-size: 0.2rem;
  position: relative;
}
.login_input_bottom {
  padding: 0 0.2rem;
}
.login_img {
  position: absolute;
  top: 30%;
  right: 20%;
}
.change_img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
  /* padding-right: 0.15rem; */
}
.img_p1 {
  font-size: 0.18rem;
  color: rgb(102, 102, 102);
  margin-bottom: 0.05rem;
}
.img_p2 {
  font-size: 0.18rem;
  color: rgb(59, 149, 233);
}
.login_input {
  width: 50%;
  height: 0.5rem;
  border: none;
}
.login_bottom {
  position: absolute;
  width: 100%;
  top: 3rem;
  left: 0;
}
.login_p1 {
  color: red;
  font-size: 0.13rem;
  padding-left: 0.2rem;
  margin-bottom: 0.1rem;
}
.login_p2 {
  color: red;
  font-size: 0.13rem;
  padding-left: 0.2rem;
}
.login_button {
  position: absolute;
  width: 92%;
  top: 3.6rem;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 0.2rem;
  font-size: 0.2rem;
}
.login_forget {
  position: absolute;
  top: 4.3rem;
  right: 0.2rem;
  font-size: 0.18rem;
  color: rgb(75, 157, 234);
}
</style>



