<template>
  <div class="msite">
    <div class="header_top">
      <router-link to="/search">
        <i class="el-icon-search"></i>
      </router-link>

      <router-link to="/home" class="msite_title">
        <span class="header_span">{{cityname}}</span>
      </router-link>
      <div class="header_right">
        <router-link to="/login" v-if="bol">
          <span class="login_span1">登录 | 注册</span>
        </router-link>

        <router-link to="/mine" v-else>
          <img src="../../../static/images/login.png" alt="" class="login_img">
        </router-link>
      </div>
    </div>
    <div class="msite_food">
      <el-carousel trigger="click" arrow="never" :autoplay="false" height="2.3rem">
        <el-carousel-item v-for="item in 2" :key="item">
          <ul class="msite_ul">
            <router-link tag="li" class="msite_li" v-for="(item, index) in foodtypes" :to="{path: '/food', query: { geohash,title: item.title}}" :key="index">
              <div class="msite_all">
                <img :src="'https://fuss10.elemecdn.com'+item.image_url" class="msite_img" alt="">
                <p class="msite_p">{{item.title}}</p>
              </div>
            </router-link>
          </ul>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="shop_list">
      <div class="shop_list_header">
        <img src="./img/home.png" class="list_header_img" alt="">
        <span class="list_header_span">附近商家</span>
      </div>
      <shoplist :geohash="geohash"></shoplist>
    </div>

    <footers></footers>
  </div>

</template>

<script type="text/ecmascript-6">
import { mapMutations } from "vuex";
import { api5, api6, api7, api8 } from "../../service/getData";
import footers from "../../components/footer/footers";
import shoplist from "../../components/common/shoplist";
import { Loading } from "element-ui";
const home = require("./img/home.png");
export default {
  name: "msite",
  data() {
    return {
      geohash: "", // city页面传递过来的地址geohash
      cityname: "", //当前城市名字;
      foodtypes: [], // 食品分类列表
      shoplist: [],
      shoplist1: []
    };
  },
  created() {
    this.geohash = this.$route.query.geohash;
    //获取当前城市的名字；
    this.axios.get(api6 + this.geohash).then(geohash => {
      // console.log(api6+geohash);
      this.cityname = geohash.data.name;
    });
    /**获取轮播图图片 */
    this.axios.get(api7).then(foodtypes => {
      this.foodtypes = foodtypes.data;
    });

    if (localStorage !== "") {
      return;
    } else {
      let str = localStorage.obj;
      let obj = str ? JSON.parse(str) : {};
      this.username = obj.username;
    }
  },
  components: {
    footers,
    shoplist
  },
  computed: {
    bol() {
      if (localStorage.obj == "") {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
/*顶部 */
.msite {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  /* position: relative; */
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-search {
  position: absolute;
  color: white;
  left: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.25rem;
  clear: both;
}
.msite_title {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 50%;
  height: 100%;
  line-height: 100%;
  transform: translate(-50%, -25%);
  color: #fff;
  text-align: center;
  margin-left: -0.3rem;
}
.header_span {
  font-size: 0.2rem;
  display: block;
  overflow: hidden;
  height: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* 顶部登录*/
.header_right {
  position: absolute;
  float: right;
  right: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  clear: both;
}

.login_span1 {
  color: white;
  font-size: 0.19rem;
  font-weight: 400;
}
.login_span2 {
  color: white;
  font-size: 0.19rem;
  font-weight: 400;
}
.login_img {
  width: 0.25rem;
  height: 0.25rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.1rem;
}

/*轮播图*/
.msite_food {
  background-color: white;
  position: absolute;
  width: 100%;
  top: 0.5rem;
  left: 0;
}
.el-carousel__indicators {
  width: 0.5rem;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.msite_ul {
  width: 100%;
  height: 2rem;
  /* border:2px solid red; */
  display: flex;
  flex-wrap: wrap;
}
.msite_li {
  width: 25%;
  height: 50%;
  padding: 0.1rem 0;
  display: flex;
  justify-content: center;

  /* border:1px solid red; */
}
.msite_all {
  width: 100%;
  height: 50%;
}
.msite_img {
  width: 0.52rem;
  height: 0.52rem;
  margin-left: 50%;
  transform: translateX(-50%);
}
.msite_p {
  text-align: center;
  font-size: 0.15rem;
  color: rgb(111, 111, 111);
}

/* 附近商家 */
.shop_list {
  position: absolute;
  top: 3rem;
  left: 0;
  border-top: 1px solid rgb(228, 228, 228);
  width: 100%;
  background-color: white;
  margin-bottom: 0.7rem;
  /* background-color: red; */
}
.shop_list_header {
  width: 100%;
  height: 0.48rem;
  position: relative;
  border-bottom: 1px solid rgb(228, 228, 228);
  overflow: hidden;
}
.list_header_img {
  width: 0.25rem;
  height: 0.25rem;
  padding-left: 0.2rem;
  padding-top: 0.13rem;
}
.list_header_span {
  position: absolute;
  width: 0.7rem;
  height: 0.48rem;
  font-size: 0.15rem;
  text-align: center;
  vertical-align: middle;
  line-height: 0.48rem;
  padding-left: 0.1rem;
  color: rgb(153, 153, 153);
  top: 50%;
  transform: translateY(-50%);
}
</style>



