<template>
   <div class="order">
    <div class="header_top">
      <router-link to="/msite">
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <span class="header_order">订单列表</span>
    </div>
   <footers></footers>
  </div>
</template>

<script type="text/ecmascript-6">
import footers from '../../components/footer/footers'
export default {
  data() {
    return {

    }
  },
  components: {
      footers
  }
}
</script>

<style scoped>
/*顶部 */
.order {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
.header_order {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left:0;
  transform: translateX(-50%);
}
</style>



