export default {
	//     // 记录当前经度纬度
	[RECORD_ADDRESS](state, {
		latitude,
		longitude
	}){
		state.latitude = latitude;
		state.longitude = longitude;
    },
	setusername(state, username) {
		state.username = username;

	},
	
	add(state, a) {
		a.count++;
		if (state.arrs.indexOf(a) == -1) {
			state.arrs.push(a)
		}
		state.pricess += a.price;
		 console.log(state.arrs)
	},
	reduce(state, a) {
		a.count--;
		for(let i=0;i<state.arrs.length;i++){
			if(state.arrs[i].count==0){
				state.arrs.splice(i,1);
				i++
			}
		}
		
		state.pricess -= a.price;
		//  console.log(state.pricess)
	}
}


