<template>
  <div class="home">
    <div class="header_top">
      <span class="header_logo" @click="reload">ele.me</span>

      <div class="header_right">
        <router-link to="/login" v-if="bol">
          <span class="login_span1">登录 | 注册</span>
        </router-link>
        <router-link to="/mine" v-else>
           <img src="../../../static/images/login.png" alt="" class="login_img">
        </router-link>
        
      </div>
    </div>

    <nav class="city_nav">
      <div class="city_nav_all">
        <span class="city_nav_all_span1">当前定位城市：</span>
        <span class="city_nav_all_span2">定位不准时，请在城市列表中选择</span>
      </div>

      <router-link :to="'/city/'+guessCityid" class="guess_city">
        <span class="guess_city_span">{{guessCity}}</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
    </nav>
    <div class="city_all">
      <div class="hot_city">
        <h4 class="city_title" >热门城市</h4>
        <ul class="hot_city_ul">
          <router-link tag="li" class="hot_city_li" v-for="item in hotCity" :to="'/city/'+item.id" :key="item.id">
           {{item.name}}
          </router-link>
        </ul>
      </div>
      

      <div class="group_city">
        <ul class="group_city_all">
          <li v-for="(value, key, index) in  sortgroupCity" :key="key" class="group_city_li1">
            <h4 class="city_title city_title1">
              {{key}}
              <span class="city_title3" v-if="index == 0">(按字母排序)</span>
            </h4>
              <ul class="groupCity_all_ul">
                <router-link tag="li" v-for="item in value" :to="'/city/'+item.id" :key="item.id" class="group_city_li2">
                  {{item.name}}
                </router-link>
              </ul>
            

          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
// import headers from "../../components/header/header";
import { api, api1, api2, api3 } from "../../service/getData";
const login = require("../../../static/images/login.png");

export default {
  name: "home",
  data() {
    return {
      data: "",
      guessCity: "", //当前城市
      guessCityid: "", //当前城市id
      hotCity: [], //热门城市列表
      groupCity: [], //所有城市列表
  
    };
  },
  methods: {
    //点击图标刷新页面
    reload() {
      window.location.reload();
    }
  },
  created() {
    //获取当前城市；
    this.axios.get(api1).then(guessCity => {
      //成功后的回调；
      console.log("成功了1");
      this.guessCity = guessCity.data.name;
      this.guessCityid = guessCity.data.id;
      //console.log(data.data);
    });
    //   //获取热门城市；
    this.axios.get(api2).then(hotCity => {
      //成功后的回调；
      console.log("成功了2");
      this.hotCity = hotCity.data;
      console.log(this.hotCity);
    });
    //获取所有城市；
    this.axios.get(api3).then(groupCity => {
      //成功后的回调；
      console.log("成功了3");
      this.groupCity = groupCity.data;
      console.log(this.groupCity);
    });
     if(localStorage!==""){
      return;
    }else{
      let str=localStorage.obj;
       let obj=  str?  JSON.parse(str):{};
       this.username=obj.username;
    }
    
    
  },
  computed: {
    //将获取的数据按照A-Z字母开头排序
    sortgroupCity() {
      let sortobj = {};
      for (let i = 65; i <= 90; i++) {
        if (this.groupCity[String.fromCharCode(i)]) {
          sortobj[String.fromCharCode(i)] = this.groupCity[
            String.fromCharCode(i)
          ];
        }
      }
      return sortobj;
    },
    bol(){
      if(localStorage.obj==""){
        return true;
      }else{
        return false;
      }
    }
  },

  components: {
    // headers
  }
};
</script>

<style scoped>
/*顶部 */
.home {
  font-family: Microsoft Yahei;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  /* position: relative; */
  top:0;
  left: 0;
  /* overflow: hidden; */
  z-index: 100;
  
}
.header_logo {
  position: absolute;
  color: white;
  left: 0.2rem;
  top: 50%;
 
  transform: translateY(-50%);
  font-size: 0.2rem;
  font-weight: 400;
  clear: both;
 
  
}
/* 顶部登录*/
.header_right {
  position: absolute;
  float:right;
  right: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  width:1rem;
  height:0.5rem;
  line-height: 0.5rem;
  text-align: center;
  clear: both;

}

.login_span1 {
  color: white;
  font-size: 0.19rem;
  font-weight: 400;
}
.login_span2 {
  color: white;
  font-size: 0.19rem;
  font-weight: 400;
}
.login_img{
  width:0.25rem;
  height:0.25rem;
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  right:0.1rem;
}
.city_nav{
  height:0.92rem;
  margin-top:0.5rem;
  position: relative;
}
/*当前定位城市*/
.city_nav_all {
  /* border:1px solid black; */
  height:0.18rem;
  width:100%;
  position: absolute;
  top:25%;
  transform: translateY(-50%);
  justify-content: space-between;
  color: rgb(102, 102, 102);
  font-size: 0.15rem;
  font-weight: 400;
  font-family: Microsoft Yahei;
  overflow: hidden;
  clear: both;
}
.city_nav_all_span1{
   position: absolute;
   left:0.2rem;
}
.city_nav_all_span2 {
  /* border:1px solid red; */
  position: absolute;
  right:0.2rem;
  color: rgb(159, 159, 159);
  font-size: 0.15rem;
  font-weight: 900;
  
}
/* 当前城市 */
.guess_city {
  width:100%;
  position: absolute;
  top:0.5rem;
  display:flex;
  justify-content: space-between;
  align-items: center;
  height: 0.32rem;

  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 2px solid rgb(228, 228, 228);
  font-size: 0.2rem;
  font-family: Microsoft Yahei;
  color: rgb(49, 144, 232);

}
.guess_city_span{
      padding: 0.2rem 0 0.2rem 0.2rem ;
}
.el-icon-arrow-right{
      position: absolute;
      top:50%;
      right:0.3rem;
      transform: translateY(-50%);
}
.city_all {
  background-color: rgb(245, 245, 245);
  color: rgb(102, 102, 102);
  overflow: hidden;
}
/* 热门城市 */
.hot_city {
  background-color: white;
  
}
.city_title{

  height: 0.35rem;
  line-height: 0.45rem;
  background-color: white;
  border-bottom: 1px solid rgb(228, 228, 228);
  font-size: 0.18rem;
  font-family: Helvetica Neue;
  font-weight: 400;
  padding-bottom: 0.1rem;
  padding-left: 0.1rem;
}

.hot_city_ul{


  overflow: hidden;
}
.hot_city_li {
  box-sizing: border-box;
  height:0.5rem;
  line-height: 0.5rem;
  width:25%;
  float: left;
  text-align: center;
  font-size: 0.18rem;
  font-weight: 450;
  color: rgb(49, 144, 232);
  border-bottom: 1px solid rgb(228, 228, 228);
  border-right: 1px solid rgb(228, 228, 228);
  
}
.hot_city_li:nth-of-type(4n){
            border-right: none;
        }

/* 所有城市*/
.group_city_all{

  overflow: hidden;
  
}
.city_title3{
  font-size:0.13rem;
  color:rgb(132, 132, 132);
  padding-left:0.1rem;


  
}
.group_city_li1{
  
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  border-top: 2px solid rgb(228, 228, 228);
  border-bottom: 2px solid rgb(228, 228, 228);
 
}
.groupCity_all_ul{
  overflow: hidden;
}
.group_city_li2{
  box-sizing: border-box;
  background-color: white;
  height:0.5rem;
  line-height: 0.5rem;
  width:25%;
  float: left;
  text-align: center;
  white-space: nowrap;
	/*设置内容不换行*/
	text-overflow: ellipsis;
	/*设置文字超出文本框的内容显示成...*/
	overflow: hidden;
	/*超出部分隐藏*/
  font-size: 0.18rem;
  font-weight: 450;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  border-right: 1px solid rgb(228, 228, 228);
  /* background-color: red; */

}
.group_city_li2:nth-of-type(4n){
            border-right: none;
        }
     
</style>



