<template>
  <div class="search">
    <div class="header_top">
      <router-link to="/msite">
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <span class="header_search">搜索</span>
    </div>
    <div class="search_input">
        <el-input v-model="input" class="serach_input1" placeholder="请输入商家或美食名称"></el-input>
        <el-button type="primary" class="search_button">提交</el-button>
    </div>
 <footers></footers>
  </div>
</template>

<script type="text/ecmascript-6">
import footers from '../../components/footer/footers'
import { api6 } from "../../service/getData";
export default {
  name:"search",
  data() {
    return {
             input: '',
              geohash: "", // city页面传递过来的地址geohash
    }
  },
   created() {
    this.geohash = this.$route.query.geohash;
   },
  components: {
       footers
  }
}
</script>

<style scoped>
/*顶部 */
.search {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
.header_search {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left:0;
  transform: translateX(-50%);
}
.search_input{
  width:100%;
  height: 0.7rem;
  position: fixed;
  top: 0.6rem;
  left: 0;
  background-color: white;
}
.serach_input1{
  position: absolute;
  top:50%;
  transform: translateY(-50%);
  width:68%;
  padding-left:0.2rem;

  

}

.search_button{
  width:0.95rem;
  position: absolute;
  top:50%;
  right:5%;
  transform: translateY(-50%);
  font-size:0.18rem;
  font-weight: 600;
 
}
</style>
<style>
.el-input__inner{
  background-color:rgb(242,242,242);
  font-weight:700;

 
}
</style>




