<script type="text/ecmascript-6">
import Vue from "vue";
import { api6, api16, api17, api18, api19, api20 } from "../../service/getData";

export default {
  name: "shop",
  data() {
    return {
      geohash: "",
      shopId: "", //商店id值
      shopDetailData: [], //商铺详情
      activities: [], //新
      menuId: "", //食品列表id值
      menuList: [], //食品列表
      isclass: true, //切换显示商品或者评价
      ratingList: [], //评价列表
      rating: null, //评价分数
      num1: "",
      num2: "",
      shows:false,
      tags: [] //评价分类
    };
  },
  computed: {
    //购物车总钱数；
    buy() {
      return this.$store.state.pricess;
    },
    //回调商品数组;
    arrs() {
      return this.$store.state.arrs;
    }
    
  },
  created() {
    this.geohash = this.$route.query.geohash;

    this.shopId = this.$route.query.id;
    console.log(this.shopId);
    this.menuId = this.$route.query.id;
    console.log(this.menuId);
    //获取当前城市的名字；
    this.axios.get(api6 + this.geohash).then(geohash => {
      this.geohash = geohash.data;
    });
    /**获取商铺详情 */
    this.axios.get(api16 + this.shopId).then(shop => {
      this.shopDetailData = shop.data;
      this.activities = shop.data.activities[0];
    });
    /**获取食品列表 */
    this.axios.get(api17 + this.menuId).then(menu => {
      for (let i = 0; i < menu.data.length; i++) {
        for (let ii = 0; ii < menu.data[i].foods.length; ii++) {
          Vue.set(menu.data[i].foods[ii].specfoods[0], "count", 0);
        }
      }
      this.menuList = menu.data;
    });
    /**获取评价信息 */
    this.axios.get(api18).then(ratingList => {
      this.ratingList = ratingList.data;
      console.log(ratingList);
    });

    /**获取评价分数 */
    this.axios.get(api19 + this.shopId + "/ratings/scores").then(rating => {
      this.rating = rating.data;
      this.num1 =
        Math.round(Math.round(this.rating.service_score * 100) / 10) / 10;
      this.num2 =
        Math.round(Math.round(this.rating.food_score * 100) / 10) / 10;
    });

    /**获取评价分类 */
    this.axios.get(api20 + this.shopId + "/ratings/tags").then(tags => {
      this.tags = tags.data;
      console.log(this.tags);
    });
  },
  computed: {},
  methods: {
    goback() {
      this.$router.push({ name: "msite" });
    },
    changeShow(e) {
      if (e.target.className == "activity_show") {
        this.isclass = this.isclass;
      } else {
        this.isclass = !this.isclass;
      }
    },
    addcount(a) {
      console.log(a.count);
      this.$store.commit("add", a);
    },
    reducecount(a) {
      this.$store.commit("reduce", a);
    },
    // 点击btn让购物车显示
     btn(){
       this.shows = !this.shows
     },
      //点击清空按钮清空购物车
  btn1(state){
      for(let i=0;i< this.$store.state.arrs.length;i++){
         this.$store.state.arrs[i].count == 0
         this.$store.state.arrs.splice(i,1);
         i--
     }
     this.prices = 0
  },

  },
  components: {}
};
</script>

