.commit_header_span4 {
  color: rgb(153, 153, 153);
  font-size: 0.2rem;
}

/*评价分类*/
.commit_center {
  margin-top: 0.2rem;
  background-color: white;
  border-bottom: 1px solid rgb(228, 228, 228);
}
.tag_list_ul {
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  padding: 0.2rem;
}
.tag_list_li {
  width: 1.2rem;
  height: 0.4rem;
  text-align: center;
  box-sizing: border-box;
  background-color: red;
  font-size: 0.16rem;
  color: #6d7885;
  padding: 0.1rem;
  background-color: #ebf5ff;
  border-radius: 0.1rem;
  margin: 0 0.1rem 0.05rem 0;
}
.tag_list_li:hover {
  background-color: #3190e8;
  color: #fff;
}
/*评价描述*/
.commit_bottom {
  width: 100%;
  /* height:3.5rem; */
  background-color: white;
  box-sizing: border-box;
  color: rgb(111, 111, 111);
}
.commit_bottom_ul {
  padding: 0.1rem;
}
.commit_bottom_li {
  position: relative;
  padding: 0.1rem;
  width: 100%;
  height: 1.8rem;
  border-bottom: 1px solid rgb(228, 228, 228);
}
.username_star {
  position: absolute;
  top: 0.2rem;
  left: 0.7rem;
  width: 100%;
}
.username {
  font-size: 0.16rem;
}
.commit_bottom_rate1 {
  padding: 0.1rem 0.1rem 0.1rem 0;
}
.time_spent_desc {
  position: absolute;
  top: 0.28rem;
  left: 1rem;
  font-size: 0.16rem;
}
.rated_time {
  position: absolute;
  top: 0;
  right: 1rem;
  font-size: 0.16rem;
}
.rating_img {
  position: absolute;
  top: 0.7rem;

  width: 0.9rem;
  height: 0.9rem;
}
.rating_img1 {
  position: absolute;
  top: 0.7rem;
  left: 1.2rem;
  width: 0.9rem;
  height: 0.9rem;
}
</style>
<style>
.el-rate__item {
  width: 0.15rem;
}
.el-rate__icon {
  font-size: 0.12rem;
}
.el-rate__text {
  font-size: 0.12rem;
}
</style>



