<template>
  <div class="footers">
    <ul class="footers_ul">
      <li v-for="(title,index) in footerTitles" class="footers_li" :key='index'>

        <router-link :to="title.to">
          <img :src="title.pic" class="footers_img" alt="">
          <p class="footers_p">{{title.name}}</p>
        </router-link>
        

      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
   import {mapState} from 'vuex'
   import {  api6 } from "../../service/getData";
   
//如果想使用静态文件，要用模板化方式引过来；
const ele = require("./img/ele.png");
import search from "./img/search.png";
const order = require("./img/order.png");
const mine=require("./img/my.png");
export default {
  name:"footers",
  data() {

    return {
       geohash: "", // city页面传递过来的地址geohash
        footerTitles:[
        { name: "外卖", to:{path: '/msite'}, pic:ele},
        { name: "搜索", to: "/search", pic: search },
        { name: "订单", to: "/order", pic: order },
        { name: "我的", to: "/mine", pic: mine }
      ]
    }
  },
  created(){
     this.geohash = this.$route.query.geohash;
     console.log(this.geohash);
         //获取当前城市的名字；
     this.axios.get(api6+this.geohash).then(geohash => {
      // console.log(geohash);
      // console.log(111);
      
    });
        

  },
  methods:{
    
  },
  computed:{
   
  },
  components: {
        
  }
}
</script>

<style scoped>
.footers{
  width:100%;
  height:0.51rem;
  position: fixed;
  bottom: 0;
  left:0;
  right: 0;
  border-top:1px solid rgb(228, 228, 228);
  padding-top:0.1rem;
  padding-bottom: 0.1rem;
  z-index: 10;
  background-color: white;
  
}
.footers_ul{
   display: flex;
    height:0.51rem;
    justify-content: space-around;
   align-items: center;
  
}
.footers_li{
  font-size:0.2rem;
  text-align: center;
}
.footers_img{
   width:0.3rem;
   height:0.3rem;
  
}
.footers_ul a{
  color:white;
  font-size:0.15rem;
  text-decoration: none;
}
.footers_p{
  
  color:rgb(102,102,102);

}
</style>



