import Vue from 'vue'
import Router from 'vue-router'
import Home from "@/page/home/home"
import City from "@/page/city/city"
import Msite from "@/page/msite/msite"

import Search from "@/page/search/search"
import Order from "@/page/order/order"
// import Profile from "@/page/profile/profile"

import Food from "@/page/food/food"
import Login from "@/page/login/login"
// import Info from "@/page/profile/children/info"


import Mine from '@/page/mine/mine'
import Download from '@/page/mine/download'
import Service from '@/page/mine/service'
import Contents from '@/page/mine/contentsall'
import Member from '@/page/mine/member'
import Integral from '@/page/mine/integral'
import Detail from '@/page/mine/detail'
import Balance from '@/page/mine/balance'
import Detail1 from '@/page/mine/detail1'
import Shopping from '@/page/mine/shopping'
import Customer from '@/page/mine/customer'
import Info from "@/page/mine/children/info"

import Shop from "@/page/shop/shop"




Vue.use(Router)

export default new Router({
  routes: [
    //首页城市列表页
    {
      path: '/',
      component: Home,
    },
     //首页城市列表页
    {
      path:"/home",
      component:Home
    },
    //当前城市页
    {
      path:"/city/:cityid",
      name:"city",
      component:City
    },
     //所有商铺列表页
     {
      path: '/msite',
      name:"msite",
      component: Msite,

     
    },
     //特色商铺列表页
     {
      path:'/food',
      name:"food",
      component:Food
    },
    //搜索页
    {
      path: '/search',
      name:"search",
      component: Search
  },
     //订单列表页
     {
      path: '/order',
      component: Order,
  },
  //我的页面
  // {
  //   path: '/profile',
  //   name:"profile",
  //   component: Profile,
  //   children:[{
  //     path: '/profile/info', 
  //     name:"info",//个人信息详情页
  //     component: Info,
  //   }]
  // },
   //登录注册页
   {
    path: '/login',
    name:"login",
    component: Login
},
// 我的页面
{
  path: '/mine',
  name: 'mine',
  component: Mine,
  children:[
    {
      path: '/mine/info', 
       name:"info",//个人信息详情页
       component: Info,
    }
  ]
 
},
{
  path: '/mine/download',
  name: 'download',
  component: Download
},
{
  path: '/mine/service',
  name: 'service',
  component: Service
},
{
  path: '/mine/member',
  name: 'member',
  component: Member
},
{
  path: '/mine/integral',
  name: 'integral',
  component: Integral
},
{
  path: '/mine/integral/detail',
  name: 'detail',
  component: Detail
},
{
  path:'/mine/balance',
  name: 'balance',
  component: Balance
},
{
  path: '/mine/balance/detail1',
  name: 'detail1',
  component: Detail1
},
{
  path: '/mine/shopping',
  name: 'shopping',
  component: Shopping
},
{
  path: '/mine/service/contentsall',
  name: 'contentsall',
  component: Contents
},
{
  path: '/mine/service/customer',
  name: 'customer',
  component: Customer
},
//商铺详情页
{
  path: '/shop',
  name:"shop",
  component: Shop,
  // children: [{
  //     path: 'foodDetail', //食品详情页
  //     component: foodDetail,
  // }, {
  //     path: 'shopDetail', //商铺详情页
  //     component: shopDetail,
  //     children: [{
  //         path: 'shopSafe', //商铺安全认证页
  //         component: shopSafe,
  //     }, ]
  // }]
}
 
  ]
})



