<template>
  <div class="food">
    <div class="header_top">
      <router-link :to="{path:'/msite',query:{geohash}}" >
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <span class="header_foodname">{{headTitle}}</span>
    </div>
    <div class="food_nav">
      <el-row>
        <el-col :span="8">
          <!-- 分类 -->
          <div class="food_list">
            <div class="list_all" @click="chooseType">
              <span :class="{'title':title}" v-show="title">{{listTitle}}</span>
              <i class="el-icon-caret-bottom" v-show="title"></i>
              <span :class="{'title':!title}" v-show="!title">分类</span>
              <i class="el-icon-caret-top" v-show="!title"></i>
            </div>
            <div v-show="showA" class="list_bottom">
              <div class="list_left">
                <ul>
                  <li v-for="(item, index) in foodlist1" :key="index" class="foodlist1" @click="selectfoodlist1_name(item.id,index)">
                    <img :src="'https://fuss10.elemecdn.com/'+item.image_url+'.png'" alt="" class="foodlist1_img">
                    <span class="foodlist1_span1">{{item.name}}</span>
                    <span class="foodlist1_span2">{{item.count}}</span>
                    <i class="el-icon-arrow-right"></i>
                  </li>
                </ul>
              </div>
              <div class="list_right">
                <ul>
                  <li v-for="(item, index) in foodlist2" :key="index" class="foodlist2">
                    <span class="foodlist2_span1">{{item.name}}</span>
                    <span class="foodlist2_span2">{{item.count}}</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="8">

          <!-- 排序 -->
          <div class="food_sort " @click="chooseType1">
            <div class="sort_all">
              <span class="sort_title">排序</span>
              <i :class="{'el-icon-caret-bottom':isB,'el-icon-caret-top':!isB}"></i>
            </div>
            <div class="sort_bottom" v-show="showB">
              <ul>
                <li v-for="(title,index) in sortList" class="sort_li" :key='index'>
                  <img :src="title.pic" alt="" class="sort_img">
                  <p class="sort_p">{{title.name}}</p>
                </li>
              </ul>
            </div>

          </div>
        </el-col>
        <el-col :span="8">
          <!-- 筛选 -->
          <div class="screen_all"  @click="chooseType2"  >
            <div class="food_screen">
              <span class="screen_title">筛选</span>
              <i :class="{'el-icon-caret-bottom':isC,'el-icon-caret-top':!isC}"></i>
            </div>
            <div class="screen_bottom" v-show="showC">
              <div class="screen_bottom1">
                <div class="screen_span1">
                  <span class="screen_span1_1">配送方式</span>
                </div>

                <ul class="screen_ul1">
                  <li class="screen_li1">
                    <img src="./img/bird.png" alt="">
                    <span>蜂鸟专送</span>
                  </li>

                </ul>
              </div>
              <div class="screen_bottom2">
                <div class="screen_span2">
                  <span class="screen_span2_1">商家属性(可以多选)</span>
                </div>
                <ul>
                  <li v-for="(item,index) in shopactive" :key="index" class="screen_li2">
                    <span :style="'color:#'+item.icon_color+';border:1px solid #'+item.icon_color" class="shopactive_span1">{{item.icon_name}}</span>
                    <span class="shopactive_span2">{{item.name}}</span>
                  </li>
                </ul>

              </div>
            </div>
            <div class="food_button" v-show="showC">
              <el-button class="button">清空</el-button>
              <el-button type="success" class="button">确定</el-button>
            </div>
          </div>

        </el-col>
      </el-row>
      <!-- 按钮 -->

    </div>
    <div class="food_shoplist">
      <shoplist></shoplist>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import shoplist from "../../components/common/shoplist";
import { api6, api7, api12, api13, api15 } from "../../service/getData";

//如果想使用静态文件，要用模板化方式引过来；
const sort = require("./img/sort.png");
const distance = require("./img/distance.png");
const hot = require("./img/hot.png");
const lowest = require("./img/lowest.png");
const time = require("./img/time.png");
const score = require("./img/score.png");

export default {
  name: "food",
  data() {
    return {
      geohash: [],
      latitude: "",
      longitude: "",
      headTitle: "", // msiet页面头部标题
      listTitle: "", // 排序左侧头部标题

      shoplist: [],
      sortBy: "", // 筛选的条件
      showA: false,
      showB: false,
      showC: false,
      isA: true,
      isB: true,
      isC: true,
      title: true,
      foodlist1: [], // 分类左侧数据
      foodlist2: [], // 分类右侧数据
      sortList: [
        { name: "智能排序", pic: sort },
        { name: "距离最近", pic: distance },
        { name: "销量最高", pic: hot },
        { name: "起送价最低", pic: lowest },
        { name: "配送速度最快", pic: time },
        { name: "评分最高", pic: score }
      ],
      shopactive: []
    };
  },
  created() {
    this.headTitle = this.$route.query.title;
    this.listTitle = this.headTitle;
    /**获取商铺排序 */
    this.axios.get(api13).then(geohash => {
      // console.log(api6+geohash);
      this.geohash = geohash.data;
      console.log(this.geohash);
    });
    /**商品分类列表 */
    this.axios.get(api12).then(foodlist1 => {
      //成功后的回调；

      this.foodlist1 = foodlist1.data;

      //console.log(data.data);
    });
    /**商家属性活动列表 */
    this.axios.get(api15).then(shopactive => {
      this.shopactive = shopactive.data;
      console.log(this.shopactive);
    });
  },
  methods: {
    // 点击顶部三个选项，展示不同的列表，选中当前选项进行展示，同时收回其他选项
    chooseType() {
      this.isA = !this.isA;
      this.isB = false;
      this.isC = false;
      this.showA = !this.showA;
      this.showB = false;
      this.showC = false;
      this.title = !this.title;
    },
    chooseType1() {
       this.isA = false;
      this.isB = !this.isB ;
      this.isC = false;
      this.showA = false;
      this.showB = !this.showB;
      this.showC = false;
    },
    chooseType2() {
       this.isA = false;
      this.isB = false;
      this.isC = !this.isC;
      this.showA = false;
      this.showB = false;
      this.showC = !this.showC;
    },
    selectfoodlist1_name(a, index) {
      this.foodlist2 = this.foodlist1.filter(
        item => a == item.id
      )[0].sub_categories;
      let b = document.getElementsByClassName("foodlist1");
      // b.style.backgroundColor='white';

      for (let i = 0; i < b.length; i++) {
        b[i].style.backgroundColor = "rgb(241, 241, 241)";
      }
      b[index].style.backgroundColor = "white";
    }
  },
  components: {
    shoplist
  }
};
</script>

<style scoped>
/*顶部 */
.city {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
/*导航栏*/
.food_nav {
  width: 100%;
  height: 0.48rem;
}
.header_foodname {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left: 0;
  transform: translateX(-50%);
}
.food_shoplist {
  position: absolute;
  top: 1rem;
}

.food_nav {
  position: fixed;
  top: 0.5rem;
  left: 0;
  width: 100%;
  height: 0.3rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  border-bottom: 1px solid rgb(228, 228, 228);
  z-index: 10;
  background-color: white;
}
.list_all:hover{
   color: rgb(49, 144, 232);
}
.food_list {
  font-size: 0.1rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  border-right: 1px solid rgb(228, 228, 228);
  overflow: hidden;
}
.food_sort {
  font-size: 0.1rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  border-right: 1px solid rgb(228, 228, 228);
}
.food_screen {
  font-size: 0.1rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
}
.food_color {
  color: rgb(49, 144, 232);
}
.list_bottom {
  position: fixed;
  top: 1rem;
  left: 0;
  width: 100%;
  height: 5.4rem;
  z-index: 10;

  background-color: rgb(241, 241, 241);
}
.list_left {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
}
/*分类*/
.foodlist1 {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
}

.list_right {
  position: absolute;
  top: 0;
  left: 50%;
  background-color: red;
  width: 50%;
  height: 5.4rem;
  color: rgb(102, 102, 102);
  background-color: white;
  overflow-x: hidden;
  overflow-y: scroll;
}
.foodlist1_img {
  position: absolute;
  transform: translateY(60%);
  left: 0.2rem;
  width: 0.25rem;
  height: 0.25rem;
}
.foodlist1_span1 {
  position: absolute;
  left: 0.6rem;
}
.foodlist1_span2 {
  margin-left: 1rem;
  color: white;
  background-color: rgb(204, 204, 204);
  border: 1px solid rgb(204, 204, 204);
  border-radius: 50%;
  padding: 0.02rem;
}
.el-icon-arrow-right {
  position: absolute;
  right: 0.2rem;
  transform: translateY(150%);
}
.foodlist2 {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  border-bottom: 1px solid rgb(204, 204, 204);
}
.foodlist2_span1 {
  position: absolute;
  left: 0.2rem;
}
.foodlist2_span2 {
  position: absolute;
  right: 0.2rem;
}

/*排序*/
.sort_all:hover{
   color:rgb(49, 144, 232);
}
.sort_bottom {
  position: fixed;
  top: 1rem;
  left: 0;
  width: 100%;
  height: 4.8rem;
  z-index: 10;
  background-color: white;
}

.sort_li {
  position: relative;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  border-bottom: 1px solid rgb(204, 204, 204);
}
.sort_img {
  position: absolute;
  width: 0.22rem;
  height: 0.22rem;
  top: 50%;
  transform: translateY(-50%);
  left: 0.2rem;
}
.sort_p {
  position: absolute;
  display: inline-block;
  top: 50%;
  transform: translateY(-50%);
  left: 0.7rem;
}
/*筛选*/
.food_screen:hover{
   color:rgb(49, 144, 232);
}
.screen_bottom {
  position: fixed;
  top: 1rem;
  left: 0;
  width: 100%;
  height: 3.2rem;
  z-index: 10;
  background-color: white;
  font-size: 0.15rem;
  color: rgb(51, 51, 51);
  border-top: 1px solid rgb(204, 204, 204);
  overflow: hidden;
}

.screen_bottom1 {
  width: 100%;
  height: 1rem;
  overflow: hidden;
}
.screen_span1 {
  padding-left: 0.2rem;
  padding-top: 0.1rem;
}

.screen_li1 {
  border: 1px solid rgb(238, 238, 238);
  border-radius: 10%;
  margin-left: 0.2rem;
  margin-top: 0.1rem;
  width: 1.2rem;
  padding: 0.1rem 0.1rem;
}

.screen_bottom2 {
  width: 100%;
  height: 1.5rem;
  overflow: hidden;
  padding: 0.1rem;
}
.screen_span2 {
  padding-left: 0.1rem;
  padding-bottom: 0.1rem;
}

.screen_li2 {
  border: 1px solid rgb(238, 238, 238);
  border-radius: 10%;
  margin-left: 0.1rem;
  margin-top: 0.1rem;
  width: 1.1rem;
  padding: 0.1rem 0.1rem;
  float: left;
}
.shopactive_span1 {
  padding: 0.02rem;
  border-radius: 20%;
  margin-right: 0.05rem;
}

/*按钮*/
.food_button {
  position: fixed;
  top: 3.5rem;
  left: 0;
  width: 100%;
  height: 0.6rem;
  background-color: rgb(241, 241, 241);
  padding: 0.1rem 0.15rem;
  z-index: 20;
}
.button {
  width: 45%;
  height: 100%;
}
</style>



